@use "../../style/Import.scss" as import;
.ModalPoki {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: rgba(33, 33, 33, 0.5);
	display: none;
	z-index: 10;
	&.active {
		display: block;
	}
}

.ModalPoki__container {
	max-width: 800px;
	max-height: 420px;
	width: fit-content;
	height: fit-content;
	position: absolute;
	top: 45%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);

	.ModalPoki__hide {
		height: fit-content;
		margin-bottom: 5px;
		width: 35px;
		.ModalPoki__hide--btn {
			display: flex;
			justify-content: flex-end;
			width: 100%;
		}
	}

	.ModalPoki__container--content {
		height: 100%;
		box-shadow: 4px 4px 8px rgba(1, 28, 64, 0.2);
		border-radius: 8px;
	}
}

.ModalPoki__content {
	display: flex;
	height: 100%;

	&--cover {
		width: 40%;
		box-shadow: 4px 4px 8px rgba(1, 28, 64, 0.2);
		padding: 16px 0;
	}

	&--description {
		width: 60%;
		padding: 29px 20px 25px 14px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
}
.ModalPoki__cover--modifiers {
	display: flex;
	width: 100%;
	justify-content: flex-end;
}
.ModalPoki__description--info {
	display: flex;
	justify-content: space-between;
	.ModalPoki__info--name {
		.ModalPoki__name--style {
			@include import.KarlaBold(#fdfdfd, 36px);
		}
	}
	.ModalPoki__info--generation {
		display: flex;
		align-items: center;
		.ModalPoki__generation--number {
			margin-right: 8px;
			.ModalPoki__generation--info {
				@include import.KarlaNormal(#fdfdfd, 24px);
			}
		}
		.ModalPoki__generation--meaning {
			background: #f2cb07;
			border-radius: 50%;
			width: 41px;
			height: 41px;
			display: flex;
			justify-content: center;
			align-items: center;
			.ModalPoki__meaning--id {
				@include import.KarlaNormal(#000000, 16px);
			}
		}
	}
}
.ModalPoki__description--abilities {
	background: #fdfdfd;
	box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
	border-radius: 8px;
	padding: 15px 28px 7px;
	.ModalPoki__abilities--title {
		.ModalPoki__abilities--text {
			@include import.KarlaNormal(#000000, 16px);
		}
	}
	.ModalPoki__abilities--meaning {
		.ModalPoki__abilities--dec {
			@include import.KarlaNormal(#000000, 16px);
		}
	}
}
.ModalPoki__description--lvl {
	background: #fdfdfd;
	box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
	border-radius: 8px;
	display: flex;
	.ModalPoki__lvl--item {
		width: 50%;
		padding: 20px 18px 7px 18px;
	}
}
.ModalPoki__description--characteristics {
	display: flex;
	justify-content: space-between;

	.ModalPoki__characteristics--item {
		padding: 8.3px 0;
		width: 20%;
		max-width: 82px;
		background: #fdfdfd;
		box-shadow: 4px 4px 24px rgba(1, 17, 38, 0.2);
		border-radius: 8px;
	}
}
@media (max-width: 768px) {
	.ModalPoki__container {
		height: 100vh;
		width: 100vw;
		max-width: none;
		max-height: none;
		border-radius: 16px;
		overflow: hidden;
		top: 0;
		left: 0;
		margin-right: 0;
		transform: translate(0%, 0%);
		.ModalPoki__hide {
			top: 10px;
			left: 10px;
			position: absolute;
			z-index: 5;
			.ModalPoki__hide--btn {
				display: flex;
				justify-content: flex-end;
				width: 100%;
			}
		}
	}
	.ModalPoki__content {
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
		&--cover {
			width: 80%;
			box-shadow: none;
			position: absolute;
			top: 5px;
			height: 40vh;
			.ModalPoki__cover--photo {
				height: 100%;
				width: auto;
				img {
					height: 100%;
					width: auto;
				}
			}
		}
		&--description {
			width: 100%;
			height: 65%;
			border-radius: 16px;
			padding-top: 7.5vh;
		}
	}
	.ModalPoki__description--info {
		.ModalPoki__info--name {
			width: fit-content;
			height: fit-content;
			position: absolute;
			top: 5%;
			left: 50%;
			margin-right: -50%;
			transform: translate(-50%, -50%);
		}
	}
	.ModalPoki__description--lvl {
		flex-direction: column;
		.ModalPoki__lvl--item {
			width: 100%;
			padding: 20px 18px 7px 18px;
		}
	}
}
